.light-mode {
  --text-primary: #1a202c;
  --text-secondary-1: #1a202c;
  --text-secondary-2: #2d3748;
  --bg-primary: #fff;
  --button-secondary: #1a202c;
  --bg-card: #fff;
  --bg-navbar: #1a202c;
  --bg-about: #f7fafc;
  --bg-footer: #1a202c;
}

.dark-mode {
  --text-primary: #fff;
  --text-secondary-1: #eaeaea;
  --text-secondary-2: #d4d4d4;
  --button-secondary: #2a2a2a;
  --bg-primary: #121212;
  --bg-card: #2a2a2a;
  --bg-navbar: #2a2a2a;
  --bg-about: #2a2a2a;
  --bg-footer: #121212;
}



* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: var(--bg-primary);
  font-family: 'Poppins', sans-serif;
