* {
    box-sizing: border-box;
}

.footer {
    width: 100%;
    padding: 2.8em 0;
    background-color: var(--bg-footer);
}

.footer_wrapper {
    width: 100%;
    background-color: var(--bg-footer);
}

.footer_content {
    width: 89%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    background-color: var(--bg-footer);
}

.footer_connect_info {
    display: flex;
    align-items: center;
    background-color: var(--bg-footer);
}

.footer_connect_heading {
    color: #fff;
    font-size: 2.7em;
    font-weight: 500;
    background-color: var(--bg-footer);
}

.footer_connect_info p {
    color: #fff;
    font-family: nanum-pen, sans-serif;
    font-weight: 400;
    font-size: 1.8em;
    padding: 0;
    margin-top: 0.3em;
    background-color: var(--bg-footer);
}

.footer_connect_icons {
    background-color: var(--bg-footer);
}

.footer_connect_icons a {
    background-color: var(--bg-footer);
}


.footer_connect_icons img {
    width: 25%;
    margin-right: 0.7em;
    background-color: var(--bg-footer);
}


@media(max-width:1440px) and (min-width:769px) {
    .footer_content {
        width: 85%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
    }

    .footer_connect_heading {
        color: #fff;
        font-size: 2.1em;
        font-weight: 500;
    }

    .footer_connect_info p {
        color: #fff;
        font-family: nanum-pen, sans-serif;
        font-weight: 400;
        font-size: 1.4em;
        padding: 0;
        margin-top: 0.3em;
    }

    .footer_connect_icons img {
        width: 40px;
        height: 40px;
        margin-right: 0.8em;
    }
}


@media(max-width:768px) {
    .footer_content {
        width: 88%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }

    .footer_connect_info {
        display: flex;
        flex-direction: column;
    }

    .footer_connect_heading {
        color: #fff;
        font-size: 2.1em;
        font-weight: 500;
    }

    .footer_connect_info p {
        color: #fff;
        font-family: nanum-pen, sans-serif;
        font-weight: 400;
        font-size: 1.4em;
        padding: 0;
        margin-top: 0.3em;
    }

    .footer_connect_icons {
        text-align: center;
        margin-top: 1em;
    }

    .footer_connect_icons img {
        width: 40px;
        height: 40px;
        margin: 0 8px;
    }
}