* {
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

.resume_link,
.resume_link:link,
.resume_link:visited {
    text-decoration: none;
}



.section_1 {
    display: flex;
    width: 100%;
    min-height: 75vh;
    background-color: var(--bg-primary);
    justify-content: center;
    align-items: center;
}

.section_1_content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 88%;
    height: 86vh;
    margin: 0 auto;
    position: relative;

}

.section_1_content_about {
    width: 54%;
    z-index: 10;
}

.section_1_content_about .name {
    color: var(--text-primary);
    width: 100%;
    font-size: 2.2em;
    font-weight: 500;
    margin-bottom: 0.4em;
}

.section_1_content_about .work {
    color: var(--text-primary);
    width: 100%;
    font-size: 3em;
    font-weight: 600;
    margin-bottom: 0.8em;
}

.resume_btn {
    width: 6.3em;
    height: 3.6rem;
    color: #fff;
    background-image: linear-gradient(#7c2ae8, #00c4cc);
    border-radius: 5px;
    font-size: 1.5rem;
    font-weight: 500;
    cursor: pointer;
    border-style: none;
}

.resume_btn:hover {
    color: rgba(255, 255, 255, 0.891);
}


.section_1_content_image {
    width: 28%;
    height: 48%;
    position: absolute;
    right: 2%;
    z-index: 10;
    text-align: center;

}

/* 321x290 */

.section_1_content_image_img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-top-left-radius: 23px;
    border-top-right-radius: 23px;
}

.vector_section_1 {
    min-width: 36%;
    height: 26%;
    background-image: url('../images/grid.png');
    /* padding: 2px; */
    background-size: 55px 55px;
    background-repeat: round;
    position: absolute;
    right: -2%;
    top: 21%;
    opacity: 0.4;
    /* object-fit: contain; */
}

.vector_section_2 {
    min-width: 28%;
    height: 26%;
    background-image: url('../images/grid.png');
    /* padding: 2px; */
    background-size: 50px 50px;
    background-repeat: round;
    position: absolute;
    left: -4%;
    bottom: 6%;
    opacity: 0.4;


}

