.navbar {
    background-color: var(--bg-navbar);
    padding: 1.5em;
    display: flex;
    justify-content: space-between;
}

.navbar_brand {
    background-color: var(--bg-navbar);
    color: #fff;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 2.2em;
    margin-left: 2.2em;
}

.navbar_span {
    color: #fff;
    margin-right: 4em;
    width: 110px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--bg-navbar);
}

.navbar_span:hover {
    cursor: pointer;
}

.navbar_span i {
    font-size: 44px;
    background-color: var(--bg-navbar);
}

.navbar_span i:first-child {
    rotate: 20deg;
}

.navbar_span i:last-child {
    transform: translateY(1rem);
    rotate: 20deg;
}

@media(max-width:1480px) {
    .navbar {
        background-color: var(--bg-navbar);
        padding: 1.3em;
        display: flex;
        justify-content: space-between;
    }

    .navbar_brand {
        color: #fff;
        font-family: 'Poppins', sans-serif;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 2.2em;
        margin-left: 2.2em;
    }
}

@media(max-width:450px) {

    .navbar {
        background-color: var(--bg-navbar);
        padding: 1em;
        display: flex;
        justify-content: space-between;
    }

    .navbar_brand {
        color: #fff;
        font-family: 'Poppins', sans-serif;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 1.4em;
        margin-left: 0.5em;
    }

    .navbar_span {
        color: #fff;
        margin-right: 0.5em;
        width: 60px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .navbar_span i {
        font-size: 24px;
        margin-bottom: 0.4em;
    }
}